"use client";
import { Card, CardContent } from "@/components/ui/card";

const accounts = [
  {
    type: "Credit Card",
    number: "**** **** **** 2598",
    balance: "$25,000",
    color: "bg-teal-600",
  },
  {
    type: "Savings",
    number: "**** **** **** 7733",
    balance: "$5,000",
    color: "bg-blue-600",
  },
  {
    type: "Wallet",
    number: "UPI - xxxxxx@ybl",
    balance: "$1,200",
    color: "bg-orange-500",
  },
];

export default function AccountsPage() {
  return (
    <div className="p-4 sm:p-6 lg:p-8">
      {/* Heading */}
      <h1 className="text-lg sm:text-xl lg:text-2xl font-semibold text-gray-800 mb-6">
        All Accounts
      </h1>

      {/* Responsive Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
        {accounts.map((acc, i) => (
          <Card
            key={i}
            className="rounded-xl bg-white shadow-md hover:shadow-lg transition"
          >
            <CardContent
              className={`rounded-t-xl p-4 sm:p-5 text-white ${acc.color}`}
            >
              <p className="text-[11px] sm:text-xs">Account Type</p>
              <h3 className="text-base sm:text-lg font-semibold">{acc.type}</h3>
              <p className="text-[11px] sm:text-xs mt-0.5">{acc.number}</p>
              <div className="mt-3 flex items-center justify-between">
                <span className="text-sm sm:text-base font-bold">{acc.balance}</span>
                <span className="text-lg">ðŸ’³</span>
              </div>
            </CardContent>

            {/* Add Details Button */}
            <div className="flex justify-end p-3">
              <button className="bg-teal-600 text-white px-3 sm:px-4 py-1.5 sm:py-2 rounded-md text-xs sm:text-sm hover:bg-teal-700 transition">
                Add Details
              </button>
            </div>
          </Card>
        ))}
      </div>
    </div>
  );
}
